* {
    box-sizing: border-box;
 }    // min-height: 0.001vw; //  Fixes Safari bug with viewport units in calc()

html {
  font-size: 0.95vh; }

body {
    display: flex;
    flex-direction: column;
    background-color: #110f26;
    color: $text;
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    min-width: 320px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 1.8rem;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }

::placeholder {
	color: #666; }

::selection {
	background-color: $accent1;
	color: #fff; }



a {
  text-decoration: none;
  &:hover,
  &:focus {
    text-decoration: none; } }


#block-projects {
  padding-top: $section-top-padding; }

#block-main {
  background: linear-gradient(100deg, rgb(61, 29, 68) 0%, rgb(25, 17, 32) 79%, rgb(35, 16, 38) 100%);
  position: relative;
  z-index: 5;
  min-height: 100vh;
  padding-top: $section-top-padding;
  > .container {
    margin-left: 0;
    padding-left: 5%;
    max-width: 88%;
    // max-width: 180rem
    &:first-child {
      position: relative; }
    .navigation {
      top: 0; } }
  &::before {
    content: " ";
    background: url(../img/normal-1-min.png);
    background-position: center  !important;
    background-repeat: no-repeat !important;
    background-size: 108rem !important;
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 1; }
  &::after {
    content: " ";
    background: url(../img/normal-2-min.png);
    background-position: center  !important;
    background-repeat: no-repeat !important;
    background-size: 108rem !important;
    position: absolute;
    animation: Neon 8s ease infinite;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; } }

@keyframes Neon {
  0% {
    opacity: 0; }
  20% {
    opacity: 1; }
  35% {
    opacity: 0.2; }
  60% {
    opacity: 1; }
  75% {
    opacity: 0.3; }
  85% {
    opacity: 1; }
  100% {
    opacity: 0; } }

.container {
  max-width: 162.5rem;
  &--thin {
    max-width: 134rem; }
  &.is-relative {
    position: relative;
    .navigation {
      right: 16.3rem; } } }


.section {
  position: relative;
  padding-top: 2rem;
  padding-bottom: 1rem;
  .section-title {
    font-size: $section-title-size;
    font-family: 'DrukText';
    letter-spacing: -0.55rem;
    text-transform: uppercase;
    line-height: 1;
    text-shadow: 15px 15px 30px rgba(17, 15, 38, 0.7);
    margin-bottom: 7rem;
    &--foreign-language {
      line-height: 1.1; }
    &.section-title__mb-null {
      margin-bottom: 0; }
    &.is-md {
      font-size: $section-title-size-md; }
    &--page-title {
      font-size: 4.8rem; } }
  .section-description {
    font-weight: 700;
    font-size: $section-description-size;
    line-height: 1.58;
    margin-bottom: 5rem; }
  .section-subtitle {
    font-size: 2.4rem; }
  .section-action {
    margin-bottom: 8rem;
    .btn {
      &:not(:last-child) {
        margin-right: 1.7rem; } }
    &--bottom {
      position: absolute;
      bottom: 0rem;
      left: 0;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      > .btn {
        font-size: 1.8rem;
        padding: 2.4rem 3.4rem;
        border-radius: 3.4rem; }
      > .hex {
        margin-bottom: 4rem;
        .btn {
          font-size: 2.5rem; } } } } }

.btn {
  display: inline-block;
  line-height: 1;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 2rem;
  transition: all 0.3s ease-in-out;
  background: #271c36;
  font-weight: bold;
  padding: 1.5rem 2.1rem;
  font-size: 1.6rem;
  &[disabled] {
    pointer-events: none;
    opacity: 0.5; }
  &:hover,
  &:focus {
    color: white;
    background: #342a43; }
  &.btn--lg {
    padding: 1.333333rem 2.125rem;
    border-radius: 3.5rem;
    padding: 2.4rem 3.4rem;
    font-size: 1.8rem; }
  &.btn--sm {
    padding: 1rem 1.6rem;
    font-size: 1.6rem; }
  &.btn--primary {
    background: linear-gradient(to right, $accent1, $accent2);
    box-shadow: 0 7px 8px -4px #662d8c, 0 10px 30px 4px rgba(207, 0, 255, 0.5), 0 4px 5px -1px #2b0950, 0 0 1px 0 rgba(207, 0, 255, 0.5), inset 0px 0px 0px 1px rgba(255,255,255, .24);
    &:hover,
    &:focus {
      box-shadow: 0 10px 10px -4px #662d8c, 0 15px 30px 4px rgba(207, 0, 255, 0.5), 0 7px 7px -1px #2b0950, 0 1px 1px 0 rgba(207, 0, 255, 0.5), inset 0px 0px 0px 1px rgba(255,255,255, .24);
      transform: translateY(-3px); }
    &:active {
      box-shadow: 0 7px 8px -4px #662d8c, 0 5px 30px 4px rgba(207, 0, 255, 0.5), 0 2px 5px -1px #2b0950, 0 1px 1px 0 rgba(207, 0, 255, 0.5);
      transform: translateY(0); } }
  &.btn--secondary {
    background: linear-gradient(to right, $primary1, $primary2);
    box-shadow: 0 7px 8px -4px #662d8c, 0 10px 30px 4px rgba(207, 0, 255, 0.5), 0 4px 5px -1px #2b0950, 0 0 1px 0 rgba(207, 0, 255, 0.5), inset 0 2px 6px 0 #ff005b;
    &:hover,
    &:focus {
      box-shadow: 0 10px 10px -4px #662d8c, 0 15px 30px 4px rgba(207, 0, 255, 0.5), 0 7px 7px -1px #2b0950, 0 1px 1px 0 rgba(207, 0, 255, 0.5), inset 0 0 8px 0 #ff005b;
      transform: translateY(-3px); }
    &:active {
      box-shadow: 0 7px 8px -4px #662d8c, 0 5px 30px 4px rgba(207, 0, 255, 0.5), 0 2px 5px -1px #2b0950, 0 1px 1px 0 rgba(207, 0, 255, 0.5), inset 0 3px 12px 0 #ff005b;
      transform: translateY(0); } }
  &--image {
    padding: 0;
    border: none;
    box-shadow: none;
    box-shadow: 0 7px 8px -4px #662d8c, 0 10px 30px 4px rgba(207, 0, 255, 0.5), 0 4px 5px -1px #2b0950, 0 0 1px 0 rgba(207, 0, 255, 0.5), inset 0 2px 6px 0 #ff005b;
    &:hover,
    &:focus {
      box-shadow: 0 10px 10px -4px #662d8c, 0 15px 30px 4px rgba(207, 0, 255, 0.5), 0 7px 7px -1px #2b0950, 0 1px 1px 0 rgba(207, 0, 255, 0.5);
      transform: translateY(-3px); }
    &:active {
      transform: translateY(0); } }
  &--icon {
    border-radius: 1.8rem;
    display: block;
    height: 7rem !important;
    padding: 0;
    height: auto; }
  &__icon {
    position: relative;
    width: 1.4em;
    height: 1.9rem;
    // top: 0.15rem
    // line-height: 0
    &--left {
      margin-left: -1.1rem;
      margin-right: 1.7rem; } }
  .btn-icon-left {}
  &--terms {
    &::before, &::after {
      content: none !important; }

    position: relative;
    z-index: 10;
    margin-top: 0px;
    color: white;
    margin-top: 5px;
    font-size: 80%;
    color: white !important;
    transition: opacity .3s ease-in-out;
    text-decoration: none !important;

    &:hover, &:focus {
      opacity: 0.6; } } }

.text-center {
	text-align: center; }
.text-right {
	text-align: right !important; }

b, strong {
	font-weight: bold; }

.box {
  background-image: linear-gradient(100deg, rgba(255, 255, 255, 0.12), rgba(255, 255, 255, 0));
  border: solid 1px rgba(255, 255, 255, 0.12);
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  border-radius: 3rem;
  width: auto;
  padding: 1.2rem 3rem 1.2rem 0.5rem;
  &__image {
    height: 9.2rem;
    width: auto;
    border-radius: 1rem; }
  .box-caption {
    padding-top: 0.888889rem;
    padding-bottom: 0.888889rem;
    padding: 0;
    font-weight: 500;
    margin-left: 1.6rem;
    line-height: 1.2;
    &--bold {
      font-weight: 600; }
    &--lg {
      margin-left: 3.2rem;
      margin-right: 2.4rem;
      line-height: 1.56; }
    p {
      margin: 0 0 1rem 0;
      &:last-child {
        margin-bottom: 0; } }
    a {
      color: $accent1; } }
  &--row {
    padding-left: 1.388889rem;
    flex-direction: row; } }

.box_first_screen {
  padding-top: 0.2rem;
  padding-bottom: 0.4rem;
  padding-left: 0.5rem;
  margin-bottom: 7.2rem;
  border-top-left-radius: 1.8rem;
  border-bottom-left-radius: 1.8rem;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border-top-right-radius: 4.2rem;
  border-bottom-right-radius: 4.2rem;
  padding-right: 2.5rem;
  margin-bottom: 7.2rem; }

.text--accent {
  color: $accent1; }


.link {
  &--bold {
    font-weight: 600; }
  &__icon-arrow-right {
    margin-left: 0.4rem;
    position: relative;
    transition: all 0.3s linear;
    left: 0;
    top: 0.2rem;
    height: 0.8em;
    width: 0.8em; }
  &:hover,
  &:focus {
    .link__icon-arrow-right {
      margin-left: 0.7rem; } } }


.icon-arrow-right {
  margin-left: 0.4rem;
  position: relative;
  transition: all 0.3s linear;
  left: 0;
  top: 0.2rem;
  height: 0.8em;
  width: 0.8em; }

a {
  &.icon-arrow-right {
    position: relative;

    &::after {
      display: inline-block;
      position: absolute;
      top: 0.07rem;
      position: absolute;
      content: "\f105";
      font-family: 'Font Awesome 5 Free';
      font-style: normal;
      font-weight: 900;
      font-size: 1.2rem;
      line-height: 0;
      top: 50%;
      transform: translateY(-50%);
      right: -0.80rem;
      transition: all 0.3s linear; }
    &:hover,
    &:focus {
      &::after {
        right: -0.95rem; } } } }

