.hex {
  position: relative;
  margin: 0 1.5rem;
  flex-shrink: 0;
  padding: 3.2rem;
  background: url(../img/shape.svg);
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  &__icon {
    width: 2.4rem; }
  .btn {
    border-radius: 50%;
    width: 7rem;
    height: 7rem;
    z-index: 10;
    padding: 1.5rem;
    font-size: 2.5rem;
    display: flex;
    justify-content: center;
    align-items: center; } }

.humans-list {
  display: flex;
  justify-content: space-between;
  .human-info {
    margin-right: 3.6rem;
    &:last-of-type {
      margin-right: 0; } } }

.human-info {
  text-shadow: 2px 2px 5px #110f26, 2px 2px 5px rgba(17, 15, 38, 0.6);
  &__title {
    font-size: 1.333333rem;
    text-transform: uppercase;
    margin-bottom: 0.444444rem;
    line-height: 1.1;
    font-size: 2.4rem;
    font-family: 'DrukText'; }
  &__subtitle {
    margin-bottom: 1rem;
    font-weight: 600;
    font-size: 1.8rem; } }



.links-list {
  display: flex;
  a {
    position: relative;
    color: white;
    transition: all 0.3s ease-in-out;
    text-decoration: none !important;
    &:not(:last-of-type) {
      margin-right: 1.5rem; }
    &::before {
      content: none !important; }
    &:hover {
      opacity: 0.4; } }
  img {
    width: 2rem;
    height: 2rem;
    object-fit: contain;
    object-position: center; } }

.burger {
  width: 2.777778rem;
  height: 2.777778rem;
  top: 3.388889rem;
  position: absolute;
  right: 4rem;
  border-radius: 4px;
  z-index: 10;
  background: none;
  border: none;
  span {
    position: relative;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    user-select: none;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -15px;
    margin-top: -1.5px;
    display: block;
    width: 30px;
    height: 1px;
    background-color: white;
    outline: 1px solid transparent;
    transition-property: background-color, transform;
    transition-duration: 0.3s;
    &::before, &::after {
      display: block;
      width: 30px;
      height: 2px;
      background-color: white;
      outline: 1px solid transparent;
      transition-property: background-color, transform;
      transition-duration: 0.3s; }
    &::before, &::after {
      position: absolute;
      content: "";
      will-change: transform; }
    &::before {
      top: -9px; }
    &::after {
      top: 9px; } }
  &.clicked span {
    background-color: transparent;
    &::before {
      transform: translateY(9px) rotate(45deg); }
    &::after {
      transform: translateY(-9px) rotate(-45deg); }
    &:before, &:after {
      background-color: #ffffff; } }
  &:hover {
    cursor: pointer; } }

