.blocker {
  z-index: 5;
  padding: 0; }


#awesome-projects {
  width: 100%;
  max-width: none;
  border-radius: 0;
  height: 100%;
  z-index: 6;
  background: #000;
  .close-modal {
    display: none; }
  .back-modal {
    position: absolute; } }

.navigation {
  font-size: 1.8rem;
  position: absolute;
  right: 10%;
  top: $section-top-padding;
  z-index: 12;
  width: auto;
  .navigation-menu {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    a {
      color: white;
      &::before {
        content: none; } }
    > li {
      margin-right: 4.8rem;
      &:last-child {
        margin-right: 0; }
      > a {
        transition: opacity 0.3s ease-in-out;
        &:hover,
        &:focus {
          opacity: 0.6;
          text-decoration: none; } } } } }
a {
  &.no-underline {
    &::before {
      content: none !important; } }
  &:not(.btn), {
    position: relative;
    &:before {
      position: absolute;
      bottom: -.1rem;
      left: 0;
      display: block;
      width: 100%;
      height: 1px;
      background-color: #CF2034;
      content: "";
      transform: scale(0);
      transition: transform .4s cubic-bezier(0.11, 0.7, 0, 1); }
    &:hover,
    &:focus {
      text-decoration: none !important;
      &:before {
        transform: scale(1); } } } }


.dropdown {
  position: relative;
  .dropdown-title {
    border-radius: 2rem;
    padding: 1rem 4.8rem 1rem 1.8rem;
    background: #271c36;
    position: relative;
    transition: all 0.3s ease-in-out;
    z-index: 2;
    position: relative;
    display: block;
    width: 100%;
    &::after {
      position: absolute;
      content: "";
      right: 1.5rem;
      top: 50%;
      transform: translateY(-50%) rotate(90deg);
      width: 2.0rem;
      height: 1.3rem;
      background: url(../img/icons/arr-right.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      transition: all 0.2s ease-in-out; }
    &:hover {
      background: #342a43;
      cursor: pointer; } }
  .dropdown-list {
    margin-top: -1.5rem;
    border-radius: 0 0  2rem 2rem;
    opacity: 0;
    // z-index: -1
    display: none;
    position: absolute;
    list-style-type: none;
    padding-left: 0;
    background: #271c36;
    left: 0;
    right: 0;
    padding-top: 1.5rem;
    padding-bottom: 2rem;
    li {
      &:first-child {
        margin-top: 1.2rem; }
      a {
        padding-top: 0.8rem;
        padding-bottom: 0.8rem;
        padding-left: 1.8rem;
        padding-left: 1.8rem;
        display: block;
        transition: all 0.3s ease-in-out;
        &:hover,
        &:focus {
          text-decoration: none;
          color: white;
          background: #332841; } } } }
  &.dropdown--open {
    .dropdown-list {
      opacity: 1;
      z-index: 1;
      display: block; }
    .dropdown-title {
      // background: #342a43
      &::after {
        transform: translateY(-50%) rotate(270deg); } } } }


.modal {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #000; }


.js-project-list {
  margin-top: 9.4rem;
  img {
    width: 100%;
    height: 50vh;
    object-fit: cover;
    border-radius: 1rem; }
  .glide__slide-description {
    margin-top: 4.8rem;
    opacity: 0;
    font-size: 1.8rem;
    transition: opacity 0.2s ease-in-out;
    line-height: 1.56;
    font-weight: 500;
    .title {
      font-family: "Montserrat";
      font-size: 3.6rem;
      line-height: 1.2;
      font-weight: 600; } }
  .glide__slide {
    opacity: 0.3;
    transition: opacity 0.2s ease-in-out;
    &.glide__slide--active {
      opacity: 1;
      .glide__slide-description {
        opacity: 1; } } }
  .glide__custom-controls {
    position: absolute;
    top: calc(40vh + 15rem);
    width: calc(100% - 0rem);
    left: 0;
    right: 0;
    margin: 0 auto;
    display: none;
    button {
      background: #271c36;
      border: none;
      border-radius: 50%;
      width: 4rem;
      height: 4rem;

      display: flex;
      align-items: center;
      justify-content: center;
