.navigation-toogle,
.burger {
  display: none;
  cursor: pointer;
  height: 51px;
  padding: 24px 14px;
  width: 58px;
  position: absolute;
  right: 0;
  z-index: 100;
  span {
    background-color: white;
    display: block;
    height: 2px;
    position: relative;
    transition: top 0.2s ease 0.15s, bottom 0.2s ease 0.15s, background-color 0.05s ease 0.15s, transform 0.25s ease 0s;
    width: 30px;
    z-index: 0;
    &:before, &:after {
      background-color: white;
      display: block;
      height: 2px;
      position: relative;
      transition: top 0.2s ease 0.15s, bottom 0.2s ease 0.15s, background-color 0.05s ease 0.15s, transform 0.25s ease 0s;
      width: 30px;
      z-index: 0; }
    &:before, &:after {
      content: "";
      transform: rotate(0);
      transform-origin: center center;
      z-index: 3; }
    &:before {
      top: -10px; }
    &:after {
      bottom: -7px; } }
  &.active span {
    background-color: transparent;
    transition: background-color 0.05s ease 0.15s;
    &:before, &:after {
      transition: top 0.2s ease 0s, bottom 0.2s ease 0s, transform 0.25s ease 0.15s; }
    &:before {
      top: 1px;
      transform: rotate(-45deg); }
    &:after {
      bottom: 2px;
      transform: rotate(45deg); } } }


